import { useEffect, useRef, useState } from "react";

import {
  Button,
  Chip,
  Link,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from "@mui/material";

import moment from "moment";

import downloadIcon from "../../../../assets/BusinessSettings/downloadIcon.svg";
// style imports
import useStyles from "../styles/muiStyles";
//import { useToastMessage } from "../../../contexts";
import { useToastMessage } from "../../../../contexts";
import { useWaitingLoader } from "../../../../contexts/waitingLoaderContext";
import { useDailog } from "../../../../contexts/DailogProvider";
import AddPayment from "../../../../Components/AllPayments/Wallet/AddPayment";

import { useInvoice } from "../../../../contexts/InvoiceProvider";
import { createInvoicePdf } from "../../../../api/Invoice";

// import ExpiryAlert from "../../ExpiryAlert";
import ExpenseCalculator from "../../ExpenseCalculator";

const sprintf = require("sprintf-js").sprintf;

const DesktopInvoices = () => {
  const { openLoader } = useWaitingLoader();
  const { handleClickOpen } = useDailog();
  const [expenseCal, setExpenseCal] = useState(false);

  const [showExpenseCalculator, toggleExpenseCalculator] = useState(false);

  const { invoices, getInvoices, getWalletBalance, walletBalance } =
    useInvoice();

  const loadInvoices = useRef(async () => {
    getInvoices();
  });

  const loadWalletBalance = useRef(async () => {
    getWalletBalance();
  });

  useEffect(() => {
    loadInvoices.current();
    loadWalletBalance.current();
  }, []);

  const classes = useStyles();
  const { setSuccessMessage, setErrorMessage } = useToastMessage();

  const downloadInvoice = async (invoiceId, invoiceName) => {
    openLoader();
    try {
      const res = await createInvoicePdf(invoiceId);
      const blob = await res.blob();
      if (blob.type.length <= 1) {
        // Create blob link to download
        const url = window.URL.createObjectURL(new Blob([blob]));
        const link = document.createElement("a");
        link.href = url;
        link.setAttribute("download", `${invoiceName}.pdf`);
        // Append to html link element page
        document.body.appendChild(link);
        // Start download
        link.click();
        // Clean up and remove the link
        link.parentNode.removeChild(link);
        setSuccessMessage("Invoice Downloaded");
      }
    } catch (err) {
      setErrorMessage(err.message);
    }
  };

  /*   const formatAmount = (amount) => {
    let amt = Math.round(amount);
    return amt
      .toFixed(2)
      .toString()
      .replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
  }; */

  const formatTwoPlace = (amount = 0) => {
    return amount
      .toFixed(2)
      .toString()
      .replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
  };

  return (
    <div className={classes["stacked-cards"]}>
      {/* <ExpiryAlert /> */}
      <div className="card-row">
        <div className="card-group widget">
          <AddPayment />
          <h3 className="widget-title">
            <svg
              width="26"
              height="22"
              viewBox="0 0 26 22"
              fill="none"
              xmlns="//www.w3.org/2000/svg"
            >
              <path
                d="M23.6563 9.65934H23.0853V6.22865C23.0853 4.82587 21.9434 3.68887 20.5455 3.68887H20.0189L20.0238 2.41405C20.0238 1.46901 19.2559 0.701172 18.3109 0.701172H3.08203C1.94504 0.701172 1.01477 1.61668 1 2.75367C1 2.76352 1 2.77336 1 2.78321V19.5871C1 20.5322 1.76784 21.3 2.71288 21.3H20.5505C21.9532 21.3 23.0902 20.1581 23.0902 18.7602V15.3246H23.6612C24.3995 15.3246 25 14.7241 25 13.9858V10.9981C24.9951 10.2598 24.3946 9.65934 23.6563 9.65934ZM3.08203 1.88247H18.3109C18.6013 1.88247 18.8425 2.11873 18.8425 2.40913L18.8376 3.68887H3.08203C2.58491 3.68887 2.1813 3.28526 2.1813 2.78813C2.1813 2.291 2.58491 1.88247 3.08203 1.88247ZM21.904 18.7553C21.904 19.5035 21.2937 20.1138 20.5455 20.1138H2.70796C2.41756 20.1138 2.17637 19.8776 2.17637 19.5822V4.66344C2.45201 4.79633 2.75718 4.87016 3.08203 4.87016H20.5455C21.2937 4.87016 21.904 5.4805 21.904 6.22865V9.65934H17.8728C17.1345 9.65934 16.534 10.2598 16.534 10.9981V13.9858C16.534 14.7241 17.1345 15.3246 17.8728 15.3246H21.904V18.7553ZM23.8138 13.9858C23.8138 14.0744 23.7449 14.1433 23.6563 14.1433H17.8778C17.7892 14.1433 17.7203 14.0744 17.7203 13.9858V10.9981C17.7203 10.9145 17.7892 10.8406 17.8778 10.8406H23.6563C23.74 10.8406 23.8138 10.9095 23.8138 10.9981V13.9858Z"
                fill="#24272C"
                stroke="#24272C"
                strokeWidth="0.75"
              />
            </svg>
            {" Wallet Balance"}
          </h3>
          {walletBalance && (
            <h2 className="amount">
              {walletBalance.currency === "INR" ? "" : "$"}{" "}
              {formatTwoPlace(Number(walletBalance.balance))}
            </h2>
          )}
          <p className="maintain">{"Maintain Min. Rs.100."}</p>
          <div className="inner-flex">
            <Button type="button" variant="contained" onClick={handleClickOpen}>
              {"Add Money"}
            </Button>
            <Button variant="outlined" href="/all-payments/wallet">
              {"View Transactions"}
            </Button>
          </div>
        </div>
        <div className="expense-calculator">
          {false && expenseCal && (
            <>
              <h3 className="title">
                {"Not Sure, whats the right amount to add to your wallet?"}
              </h3>

              <Link
                className="btn-calc"
                component="button"
                underline="none"
                onClick={() => toggleExpenseCalculator(true)}
              >
                <span>{"Try Expense Calculator"}</span>
                <svg
                  width="14"
                  height="10"
                  viewBox="0 0 14 10"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    fillRule="evenodd"
                    clipRule="evenodd"
                    d="M11.3692 4.19223L8.28352 1.00205L9.25275 0L14 4.90798L9.43077 10L8.46154 8.99796L11.3692 5.62372H0V4.19223H11.3692Z"
                    fill="#12B0EE"
                  />
                </svg>
              </Link>
            </>
          )}
          <ExpenseCalculator
            open={showExpenseCalculator}
            closeDrawer={() => toggleExpenseCalculator(false)}
            setExpenseCal={(val) => setExpenseCal(val)}
          />
        </div>
      </div>
      <div className="card-group">
        <h3 className="card-title">{"Invoices"}</h3>
        {invoices.length > 0 ? (
          <TableContainer>
            <Table aria-label="Invoices" className="invoices">
              <TableHead>
                <TableRow>
                  <TableCell align="center">{"Inv No."}</TableCell>
                  <TableCell align="center">{"Date"}</TableCell>
                  <TableCell align="center">{"Amount"}</TableCell>
                  <TableCell align="center">{"Status"}</TableCell>
                  <TableCell align="center">{"Action"}</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {invoices.map((invoice, index) => {
                  const invoiceName =
                    invoice.invoice_prefix +
                    sprintf("%05s", invoice.invoice_number);
                  return (
                    <TableRow key={index} hover>
                      <TableCell align="center">{invoiceName}</TableCell>
                      <TableCell align="center">
                        {moment(invoice.created_at).format("D MMM YYYY")}
                      </TableCell>
                      <TableCell align="center">
                        
                        {formatTwoPlace(
                          Number(invoice.billing_amount.payable_amount)
                        )}
                      </TableCell>
                      <TableCell align="center">
                        <Chip
                          label="PAID"
                          sx={{
                            // backgroundColor: "#E02020",
                            backgroundColor: "#12B347",
                            color: "#ffffff",
                            height: "22px",
                          }}
                        />
                      </TableCell>
                      <TableCell align="center">
                        <Link
                          component="button"
                          underline="none"
                          className="report-download"
                          onClick={() =>
                            downloadInvoice(invoice.id, invoiceName)
                          }
                        >
                          <img src={downloadIcon} alt="download" />
                        </Link>
                      </TableCell>
                    </TableRow>
                  );
                })}
              </TableBody>
            </Table>
          </TableContainer>
        ) : (
          "No invoice available."
        )}
      </div>
    </div>
  );
};

export default DesktopInvoices;
