import React, { Fragment, useState, useEffect } from "react";
import { Hidden } from "@material-ui/core";

import MobileInvoices from "../../../Container/AllPayments/Invoices/Mobile";
import DesktopInvoices from "../../../Container/AllPayments/Invoices/Desktop";

import Layout from "../Layout";
import { useToastMessage } from "../../../contexts";

import WalletAPI from "../../../api/Wallet";

interface IAllPaymentsInvoices {
  invoices: any;
  wallet: any;
}

const AllPaymentsInvoices: React.FunctionComponent<IAllPaymentsInvoices> = (
  props
) => {
  const { setSuccessMessage, setErrorMessage } = useToastMessage();
  //const [invoices, setInvoices] = useState([]);

  return (
    <Fragment>
      <Hidden smUp implementation="js">
        <MobileInvoices />
      </Hidden>
      <Hidden xsDown implementation="js" only="xs">
        <Layout useFluid>
          <DesktopInvoices />
        </Layout>
      </Hidden>
    </Fragment>
  );
};

export default AllPaymentsInvoices;
