import { useState, useEffect } from "react";
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  TextField,
} from "@mui/material";

import { loggedUserDetail } from "../../../api/Auth";

import PaymentForm from "./PaymentForm";

import { useDailog } from "../../../contexts/DailogProvider";

import CircularProgress from "@mui/material/CircularProgress";
import Box from "@mui/material/Box";

function AddPayment(props) {
  const { open, handleClose } = useDailog();

  const [amount, setAmount] = useState(0);
  const [gotToPay, setGoToPay] = useState(false);
  const [gstAmt, setGstAmt] = useState(null);
  const [userDetail, setUserDetail] = useState(null);

  const [load] = useState(false);

  const loadLoggedUserDetail = async () => {
    const res = await loggedUserDetail();
    if (res.success) setUserDetail(res.payload);
  };

  useEffect(() => {
    loadLoggedUserDetail();
  }, []);

  const openPayModal = async (amount) => {
    // const businessId = localStorage.getItem('businessId');
    let gstAmt = amount * 1.18 - amount;
    gstAmt = gstAmt.toFixed(2);
    setGstAmt(gstAmt);
    setGoToPay(true);
    // const businessId = localStorage.getItem('businessId');
    // let gstAmt = amount * 1.18 - amount;
    // gstAmt = gstAmt.toFixed(2);
    //  window.location.href = `/payu-form?amount=${amount}&udf1=${businessId}&udf2=wallet&udf3=${gstAmt}`;
  };
  return (
    <div>
      <Dialog
        // fullScreen={fullScreen}
        open={open}
        onClose={handleClose}
      >
        <DialogTitle>{load ? "" : "Add Wallet Payment"}</DialogTitle>
        {gotToPay && (
          <PaymentForm
            paidAmount={amount}
            udf1={localStorage.getItem("businessId")}
            udf2="wallet"
            udf3={gstAmt}
            udf4="hii"
            user={userDetail}
          />
        )}
        {!load && (
          <>
            <DialogContent style={{ paddingTop: "20px" }}>
              {/*             <form
      action="https://test.payu.in/_payment"
      method="post"
      style={{ display: "block" }}
    >
      key
      <input type="text" name="key" value="JBZaLc" />
      
      txn
      <input type="text" name="txnid" value="abc14" />
      amount
      <input type="text" name="amount" value="100" />
      product
      <input type="text" name="productinfo" value="product" />
      Name
      <input type="text" name="firstname" value="nagendra" />
      email
      <input type="text" name="email" value="nagendra@gmail.com" />
      phone


      <input type="text" name="udf1" value="1" />
      <input type="text" name="udf2" value="2" />
      <input type="text" name="udf3" value="3" />
      <input type="text" name="udf4" value="4" />
      <input type="text" name="udf5" value="5" />
      <input type="text" name="phone" value="9782516453" />
      surl
      <input type="text" name="surl" value="http://127.0.0.1:3333/api/v1/payu/payment-response"  />
      furl
      <input type="text" name="furl" value="http://127.0.0.1:3333/api/v1/payu/wallet-payment-response"  />
      <input type="text" name="hash" value="8bfb7f1918de9ba3d8cf530223303c2a51a48e779cf9fb4965752c49efc6d68ed760b1b53c0cf351bd741f39548e8d0e086748608bdb53d2818bf3af6e07459e" />
      <button type="submit">Pay</button>
    </form> */}

              <TextField
                variant="outlined"
                placeholder="Enter Amount"
                id="amount"
                name="amount"
                type="number"
                value={amount}
                onChange={(e) => setAmount(e.target.value)}
                // className={classes.inpEmail}
              />
            </DialogContent>
            <DialogActions>
              <Button
                onClick={(e) => {
                  openPayModal(amount);
                }}
              >
                Add
              </Button>
            </DialogActions>
          </>
        )}
        {load && (
          <DialogContent>
            <Box
              sx={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                flexDirection: "column",
              }}
            >
              <CircularProgress />
              <span>Downloading</span>
            </Box>
          </DialogContent>
        )}
      </Dialog>
    </div>
  );
}

export default AddPayment;
