import { AuthenticatedHeaders } from "utils/ApiHeaders";
import { API_V2 } from "../Backend";

export const getHash = async (data) => {
  try {
    const res = await fetch(`${API_V2}/ccavenue/payment/create-order`, {
      method: "POST",
      headers: AuthenticatedHeaders(),
      body: JSON.stringify(data),
    });
    return await res.json();
  } catch (err) {
    return { message: err.message };
  }
};
