import React, { Fragment } from "react";
import {
  Link,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from "@mui/material";
import { Close } from "@mui/icons-material";

const formatTwoPlace = (amount) => {
  return amount
    .toFixed(2)
    .toString()
    .replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
};

const CostBreak = ({
  data: {
    CIS,
    BIS,
    ESS,
    TAX,
    currencySymbol,
    customerSessions,
    businessSessions,
    amountTotal,
    amountTax,
  },
  doClose,
}) => (
  <Fragment>
    <div className="header">
      <h1 className="title">{"Cost Break Up"}</h1>
      <Link component="button" underline="none" onClick={doClose}>
        <Close sx={{ color: "#24272C" }} />
      </Link>
    </div>
    <div className="content">
      <TableContainer>
        <Table aria-label="Invoices" className="invoices">
          <TableHead>
            <TableRow>
              <TableCell
                sx={{ borderColor: "#24272C1A", py: "10px", px: "5px" }}
              >
                {"Particulars"}
              </TableCell>
              <TableCell
                align="right"
                sx={{ borderColor: "#24272C1A", py: "10px", px: "5px" }}
              >
                {"Qty."}
              </TableCell>
              <TableCell
                align="right"
                sx={{ borderColor: "#24272C1A", py: "10px", px: "5px" }}
              >
                {"Rates"}
              </TableCell>
              <TableCell
                align="right"
                sx={{ borderColor: "#24272C1A", py: "10px", px: "5px" }}
              >
                {"Amount"}
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            <TableRow>
              <TableCell sx={{ border: 0, py: "10px", px: "5px" }}>
                {"Customer Initiated Sessions."}
              </TableCell>
              <TableCell
                align="right"
                sx={{ border: 0, py: "10px", px: "5px" }}
              >
                {customerSessions}
              </TableCell>
              <TableCell
                align="right"
                sx={{ border: 0, py: "10px", px: "5px" }}
              >
                {`${currencySymbol} ${Intl.NumberFormat("en", {
                  minimumFractionDigits: 2,
                }).format(CIS)}`}
              </TableCell>
              <TableCell
                align="right"
                sx={{ border: 0, py: "10px", px: "5px" }}
              >
                {`${currencySymbol} ${Intl.NumberFormat("en", {
                  minimumFractionDigits: 2,
                }).format(customerSessions * CIS)}`}
              </TableCell>
            </TableRow>
            <TableRow>
              <TableCell sx={{ border: 0, py: "10px", px: "5px" }}>
                {"Business Initiated Sessions."}
              </TableCell>
              <TableCell
                align="right"
                sx={{ border: 0, py: "10px", px: "5px" }}
              >
                {businessSessions}
              </TableCell>
              <TableCell
                align="right"
                sx={{ border: 0, py: "10px", px: "5px" }}
              >
                {`${currencySymbol} ${Intl.NumberFormat("en", {
                  minimumFractionDigits: 2,
                }).format(BIS)}`}
              </TableCell>
              <TableCell
                align="right"
                sx={{ border: 0, py: "10px", px: "5px" }}
              >
                {`${currencySymbol} ${Intl.NumberFormat("en", {
                  minimumFractionDigits: 2,
                }).format(businessSessions * BIS)}`}
              </TableCell>
            </TableRow>
            <TableRow sx={{ borderBottom: 1, borderColor: "#24272C1A" }}>
              <TableCell sx={{ border: 0, py: "10px", px: "5px" }}>
                {"Easy Social Sessions"}
              </TableCell>
              <TableCell
                align="right"
                sx={{ border: 0, py: "10px", px: "5px" }}
              >
                {customerSessions + businessSessions}
              </TableCell>
              <TableCell
                align="right"
                sx={{ border: 0, py: "10px", px: "5px" }}
              >
                {`${currencySymbol} ${Intl.NumberFormat("en", {
                  minimumFractionDigits: 2,
                }).format(ESS)}`}
              </TableCell>
              <TableCell
                align="right"
                sx={{ border: 0, py: "10px", px: "5px" }}
              >
                {`${currencySymbol} ${Intl.NumberFormat("en", {
                  minimumFractionDigits: 2,
                }).format((customerSessions + businessSessions) * ESS)}`}
              </TableCell>
            </TableRow>
            <TableRow>
              <TableCell colSpan={3} sx={{ border: 0, py: "10px", px: "5px" }}>
                {"Total"}
              </TableCell>
              <TableCell
                align="right"
                sx={{ border: 0, py: "10px", px: "5px" }}
              >
                {`${currencySymbol} ${Intl.NumberFormat("en", {
                  minimumFractionDigits: 2,
                }).format(amountTotal)}`}
              </TableCell>
            </TableRow>
            <TableRow sx={{ borderBottom: 1, borderColor: "#24272C1A" }}>
              <TableCell colSpan={3} sx={{ border: 0, py: "10px", px: "5px" }}>
                {`GST -${TAX}%`}
              </TableCell>
              <TableCell
                align="right"
                sx={{ border: 0, py: "10px", px: "5px" }}
              >
                {`${currencySymbol} ${formatTwoPlace(amountTax)}`}
              </TableCell>
            </TableRow>
            <TableRow>
              <TableCell colSpan={3} sx={{ border: 0, py: "10px", px: "5px" }}>
                {"Total Payable"}
              </TableCell>
              <TableCell
                align="right"
                sx={{ border: 0, py: "10px", px: "5px" }}
              >
                {`${currencySymbol} ${formatTwoPlace(amountTotal + amountTax)}`}
              </TableCell>
            </TableRow>
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  </Fragment>
);

export default CostBreak;
