import React, { useCallback, useEffect, useState } from "react";

import {
  Collapse,
  Drawer,
  IconButton,
  Link,
  TextField,
  MenuItem,
} from "@mui/material";
import { RAZERPAY_KEY } from "../../../Backend";

import { UIButton } from "../../../Components/exports";
import { CustomTextFieldFilled } from "../../../Components/UI/CustomTextField/CustomTextField";
import { useToastMessage } from "../../../contexts";

import CustomIcon from "../../../shared-components/CustomIcon/CustomIcon";

import NumberField from "./NumberField";
import CostBreak from "./cost-break";
import useStyles from "./styles/muiStyles";
import countriesList from "../../../data/countries.json";
import WalletAPI from "../../../api/Wallet";
import { useInvoice } from "../../../contexts/InvoiceProvider";

const ExpenseCalculator = ({
  open,
  closeDrawer,
  isMobile = false,
  setExpenseCal,
}) => {
  const classes = useStyles();
  const { setSuccessMessage, setErrorMessage } = useToastMessage();
  const {
    invoices,
    setInvoices,
    walletBalance,
    setWalletBalance,
    walletOrders,
    setWalletOrders,
  } = useInvoice();

  const [CIS, setCIS] = useState(0);
  const [BIS, setBIS] = useState(0);
  const [ESS, setESS] = useState(0);
  const [TAX, setTAX] = useState(0);
  const [businesses, setBusinesses] = useState([]);
  const [businessId, setBusinessId] = useState("");
  const [planName, setPlanName] = useState("");

  const [showBreakUp, toggleBreakUp] = useState(false);
  const [amountTotal, setAmountTotal] = useState(0);
  const [amountTax, setAmountTax] = useState(0);
  const [country, setCountry] = useState("India");
  const [currency, setCurrency] = useState("INR");
  const currencySymbol = currency === "INR" ? "" : "$";
  const [customerSessions, setCustomerSessions] = useState(1000);
  const [businessSessions, setBusinessSessions] = useState(1000);
  /* const getRates = useCallback(async () => {
    if (country && businessId) {
      const res = await WalletAPI.getWACharge({ businessId, country });
      if (res.error) {
        return setErrorMessage(res.error);
      } else {
        setPlanName(res.plan_name);
        setCountry(res.country);
        setCurrency(res.currency);
        setBIS(res.BISCharge);
        setCIS(res.CISCharge);
        setESS(res.es_session_rate);
        let taxRate;
        if (res.country === "India") {
          console.log("country", res.country);
          taxRate = res.gst_rate;
        } else {
          taxRate = 0;
        }
        setTAX(taxRate);
      }
    }
  }, [country, businessId]); */

  /* useEffect(() => {
    getRates();
  }, [getRates]); */

  const BusinessWithEngagePlan = async () => {
    const res = await WalletAPI.getActiveEngageBusiness();
    if (res.error) {
      return setErrorMessage(res.message);
    } else {
      if (res.payload.length > 0) {
        setBusinessId(res.payload[0]["businessId"]);
        //getRates();
        setBusinesses(res.payload);
        setExpenseCal(true);
      } else {
        setExpenseCal(false);
      }
    }
  };

  useEffect(() => {
    BusinessWithEngagePlan();
  }, []);

  useEffect(() => {
    const Total =
      customerSessions * CIS +
      businessSessions * BIS +
      (customerSessions + businessSessions) * ESS;
    const Tax = (Total * TAX) / 100;
    setAmountTotal(Total);
    setAmountTax(Tax);
  }, [customerSessions, businessSessions, country, businessId, ESS, TAX]);

  // use setAmount to set value of the amount
  false && console.log(855);

  const formatTwoPlace = (amount) => {
    return amount
      .toFixed(2)
      .toString()
      .replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
  };

  useEffect(() => {
    const script = document.createElement("script");
    script.src = "https://checkout.razorpay.com/v1/checkout.js";
    script.async = true;
    document.body.appendChild(script);
  }, []);

  const openPayModal = async (amount) => {
    console.log("amount", amount);
    const res = await WalletAPI.addWalletBalance({ businessId, amount });
    if (res.error) {
      return setErrorMessage(res.error);
    }
    const options = {
      key: RAZERPAY_KEY,
      name: "Easy Social",
      description: "wallet payment",
      order_id: res.id,
      handler: async (response) => {
        try {
          const paymentId = response.razorpay_payment_id;
          const captureResponse = await WalletAPI.captureRazerpayOrder(
            paymentId,
            { businessId, amount }
          );
          console.log("captureResponse", captureResponse);
          const paymentStatus = captureResponse.paymentData.status;
          if (paymentStatus === "captured") {
            const addedBalance = captureResponse.data.balance;
            setWalletBalance({ ...walletBalance, balance: addedBalance });
            const newInvoice = captureResponse.data.newInvoice;
            setInvoices([newInvoice, ...invoices]);
            const newOrder = captureResponse.data.newOrder;
            setWalletOrders([newOrder, ...walletOrders]);
            setSuccessMessage("amount added in wallet");
          } else {
            setErrorMessage("failed");
          }
        } catch (err) {
          return setErrorMessage(err);
        }
      },
      theme: {
        color: "#686CFD",
      },
    };
    console.log("options", options);
    const rzp1 = new window.Razorpay(options);
    rzp1.open();
  };

  return (
    <Drawer
      anchor={"right"}
      open={open}
      onClose={(ev, reason) =>
        !(reason === "backdropClick" || reason === "escapeKeyDown") &&
        closeDrawer()
      }
      className={classes["expense-calculator"]}
      sx={{ zIndex: "modal" }}
    >
      <div className="wrapper">
        <div className="header">
          <IconButton
            aria-label="Back"
            className="back-btn"
            onClick={() => {
              toggleBreakUp(false);
              closeDrawer();
            }}
            style={{ color: "transparent", padding: 0 }}
            disableFocusRipple={true}
            disableRipple={true}
          >
            <CustomIcon.BackBtnIcon />
          </IconButton>
          <h1 className="title">{"Expense Calculator"}</h1>
        </div>
        <div className="calculator">
          <div className="main">
            <div className="info">
              <h6 className="info-heading">
                {"Selected Engage - ChatBot Plan"}
              </h6>
              <h5 className="basic-heading">{planName}</h5>
            </div>
            <div className="field">
              <CustomTextFieldFilled
                select
                label="Business"
                placeholder="Business"
                variant="filled"
                type="select"
                inputProps={{ "aria-label": "Without label" }}
                value={businessId}
                fullWidth
                onChange={({ target: { value } }) => setBusinessId(value)}
              >
                <MenuItem value="" disabled={true}>
                  {"Select Business"}
                </MenuItem>
                {businesses.map((business, index) => {
                  return (
                    <MenuItem key={index} value={business.businessId}>
                      {business.business_name}
                    </MenuItem>
                  );
                })}
              </CustomTextFieldFilled>
            </div>
            <div className="field">
              <CustomTextFieldFilled
                select
                label="Country"
                placeholder="Country"
                variant="filled"
                type="select"
                inputProps={{ "aria-label": "Without label" }}
                value={country}
                fullWidth
                onChange={({ target: { value } }) => setCountry(value)}
              >
                <MenuItem value="" disabled={true}>
                  {"Select Country"}
                </MenuItem>
                {countriesList.map((country, index) => {
                  return (
                    <MenuItem key={index} value={country.name}>
                      {country.name}
                    </MenuItem>
                  );
                })}
              </CustomTextFieldFilled>
            </div>
            <div className="field">
              <h6 className="info-heading">
                {"Additional Customer Initiated Sessions"}
              </h6>
              <NumberField
                value={customerSessions}
                setValue={setCustomerSessions}
                min={100}
                max={10000000}
                step={100}
              />
            </div>
            <div className="field">
              <h6 className="info-heading">
                {"Additional Business Initiated Sessions"}
              </h6>
              <NumberField
                value={businessSessions}
                setValue={setBusinessSessions}
                min={100}
                max={10000000}
                step={100}
              />
            </div>
            <div className="field">
              <h6 className="basic-heading">{"Amount to be added"}</h6>
              <TextField
                className="big-value"
                variant="filled"
                inputProps={{ "aria-label": "Amount" }}
                readOnly={true}
                value={`${currencySymbol} ${formatTwoPlace(
                  amountTotal + amountTax
                )}`}
              />
            </div>
          </div>
          <div className="actions">
            <Link
              component="button"
              underline={showBreakUp ? "always" : "none"}
              onClick={() => toggleBreakUp(!showBreakUp)}
            >
              {showBreakUp ? "Hide cost break up" : "Show cost break up"}
            </Link>
            <UIButton
              type="button"
              onClick={(e) => {
                openPayModal(amountTotal);
              }}
            >
              {"Add to wallet"}
            </UIButton>
          </div>
        </div>
      </div>
      <Collapse orientation={"horizontal"} in={showBreakUp}>
        <div className="break-up-wrapper">
          <CostBreak
            data={{
              CIS,
              BIS,
              ESS,
              TAX,
              currencySymbol,
              customerSessions,
              businessSessions,
              amountTotal,
              amountTax,
            }}
            doClose={() => toggleBreakUp(false)}
          />
        </div>
      </Collapse>
    </Drawer>
  );
};

export default ExpenseCalculator;
