import React, { useState } from "react";
import { Button, TextField } from "@mui/material";
import { Add, Remove } from "@mui/icons-material";

const NumberField = ({
  value,
  setValue,
  min = 1,
  max = Infinity,
  step = 1,
  editableInput = true,
}) => {
  const [timeout, updateTimeOut] = useState(null);
  const increaseValue = (speed) => {
    setValue((oldValue) => (oldValue < max ? oldValue + step : max));
    updateTimeOut(setTimeout(() => increaseValue(speed * 0.8), speed));
  };
  const decreaseValue = (speed) => {
    setValue((oldValue) => (oldValue > min ? oldValue - step : min));
    updateTimeOut(setTimeout(() => decreaseValue(speed * 0.8), speed));
  };
  const buttonPress = (doAction, speed = 100) => doAction(speed);
  const buttonRelease = () => clearTimeout(timeout);
  return (
    <div className="number-field">
      <Button
        type="button"
        className="btn-updater"
        variant="contained"
        onMouseDown={() => buttonPress(decreaseValue)}
        onMouseUp={() => timeout && buttonRelease()}
      >
        <Remove sx={{ color: "#24272C" }} />
      </Button>
      <TextField
        variant="filled"
        inputProps={{ "aria-label": "Number" }}
        readOnly={!editableInput}
        value={value}
        onChange={({ target: { value } }) => editableInput && setValue(value)}
      />
      <Button
        type="button"
        className="btn-updater"
        variant="contained"
        onMouseDown={() => buttonPress(increaseValue)}
        onMouseUp={() => timeout && buttonRelease()}
      >
        <Add sx={{ color: "#24272C" }} />
      </Button>
    </div>
  );
};

export default NumberField;
